.app-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.app-main {
    position: relative;
    flex: 1 1 0rem;
}
.app-main.green-theme {
    background-size: 15% !important;
}
.app-main.blue-theme {
    background-size: 40% !important;
}
.app-main.orange-theme {
    background-size: 20% !important;
}
.app-main.pink-theme {
    background-size: 30% !important;
}

.main-content-wrapper {
    max-width: 30rem;
    margin: 0 auto;
}

.material-card-style {
    margin: 1.5rem 0;
    padding: 1rem;
    border-radius: 5px;
    background-color: white;
    box-shadow: 0 2px 4px rgba(0,0,0,.501961);
}
.material-card-style:not(:last-child) {
    margin-bottom: 1rem;
}

.app-form {
    margin-top: 1.5rem;
}
.app-form-input.MuiFormControl-root {
    margin-bottom: 1rem;
}

.button-wrapper {
    display: flex;
    justify-content: center;
}
.button-wrapper.add-margin-top {
    margin-top: 1rem;
}

.fixed-bottom-right-button.MuiIconButton-root {
    position: fixed;
    right: 1rem;
    bottom: 1rem;
    background-color: rgba(250, 250, 250, 0.9);
}
.fixed-bottom-right-button.MuiIconButton-root:hover {
    background-color: white;
}
.fixed-bottom-right-button .MuiSvgIcon-root {
    height: 2em;
    width: 2em;
}

.text-decoration-line-through {
    text-decoration: line-through !important;
}
